#cardTitle {
  font-size: larger;
  font-weight: bolder;
  color: rgb(255, 255, 255);
}

#cardImg {
  width: 80px;
  height: 80px;
  margin: 0;
}

#cardSub {
  color: rgb(155, 155, 155);
}

#serverStats, #jumboinfo {
  margin: 20px;
  background-color: #2f3136;
  color: #ffffff;
  text-shadow: 2px 2px 4px#3d3d3d;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.72);
}

#serverStats {
  margin-bottom: 80px;
}

#jumboinfo {
  margin-top: 80px;
}

html, .App, body  {
  background-color: rgb(65, 30, 30);
  color: white;
}

#navbar1 {
  margin-bottom: 100px;
  background-color: #272727 !important;
  text-shadow: 2px 2px 4px#000000;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.72);
}

#navbarbrand1 {
  color: #ffffff !important;
  text-shadow: 2px 2px 4px#000000;
  font-size: 3em;
  font-weight: 500;
  margin-left: 30px;
}

#navbarbrand2 {
  margin-left: 210px;
  color: #5f5f5f !important;
  text-shadow: 2px 2px 4px black;
}

#logbutton {
  float: right;
  margin: 10px;
}

#Footer1 {
  position: fixed;
  bottom: 0;
  height: 70px;
  width: 100vw;
  margin-bottom: 0;
}

#footerlink {
  color: orange;
}

#serverStats:hover {
  transform: translate(-4px, -4px);
  box-shadow: 8px 8px 24px rgba(0, 0, 0, 0.72);
}

#loginForm {
  width: 600px;
  height: 400px;
  margin: auto;
}